<template>
    <v-page>
        <div slot="header">
            <v-header>
                <v-btn></v-btn>
                <h1>快速注册</h1>
            </v-header>
        </div>
        <div class="regPwd-container">
            <v-step-progress :activeIndex="activeIndex" :stepList="stepArr"></v-step-progress>
            <div class="cell-wrap">
                <v-cell-box :hasBorder="true">
                    <span class="title" slot="left">登陆密码</span>
                    <input type="password"
                           placeholder="请设置登陆密码"
                           class="input-box"
                           slot="middle"
                           v-model="regInfo.pwd"
                    >
                </v-cell-box>
                <v-cell-box>
                    <span class="title" slot="left">重复密码</span>
                    <input type="password"
                           placeholder="再次输入登陆密码"
                           class="input-box"
                           slot="middle"
                           v-model="regInfo.rePwd"
                    >
                </v-cell-box>
            </div>
            <div class="tips" v-show="showTips">
                <div class="symbol-wrap">
                    <v-symbol :name="'warn'"></v-symbol>
                </div>
                <p>两次密码不一致，请重新输入!</p>
            </div>
            <div class="btn-wrap">
                <v-linear-button btnTxt="下一步"
                                 :disabled="!canNext"
                                 @click="jumpRegResult"
                ></v-linear-button>
            </div>
        </div>
    </v-page>
</template>

<script>
    import {vStepProgress} from '../../component/stepProgress'
    import {vCellBox} from '../../component/cell'
    import {vLinearButton} from '../../component/linearButton'
    export default {
        name: 'quickReg',
        components: {
            vStepProgress,
            vCellBox,
            vLinearButton
        },
        data () {
            return {
                // 步骤条激活index
                activeIndex: 1,
                // 进度条数组
                stepArr: [
                    {
                        txt: '信息验证'
                    },
                    {
                        txt: '密码设置'
                    },
                    {
                        txt: '注册成功'
                    }
                ],
                // 是否勾选同意协议
                isAgree: false,
                // 注册输入信息
                regInfo: {
                    pwd: '',      // 登陆密码
                    rePwd: ''    // 第二次输入登陆密码
                }
            }
        },
        methods: {
            // 勾选同意协议
            checkProtocol () {
                this .isAgree = !this.isAgree
            },
            // 跳转注册结果页
            jumpRegResult () {
                let jumpUrl = '/login/quickReg/quickRegResult'
                window.T.go(jumpUrl)
            }
        },
        computed: {
            // 是否能点击下一步
            canNext () {
                return this.regInfo.pwd !== '' &&
                       this.regInfo.rePwd !== '' &&
                       this.regInfo.pwd === this.regInfo.rePwd
            },
            // 是否显示提示信息
            showTips () {
                return this.regInfo.pwd !== '' && this.regInfo.rePwd !== '' && this.regInfo.pwd !== this.regInfo.rePwd
            }
        }
    }
</script>

<style lang="scss" scoped>
    .regPwd-container {
        .cell-wrap {
            font-size: px2rem(32);
            span {
                display: block;
            }
            .title {
                width: px2rem(160);
                margin-right: px2rem(50);
                line-height: px2rem(44);
            }
            .info {
                line-height: px2rem(36);
            }
            input {
                width: 100%;
                font-size: px2rem(32);
            }
        }
        .tips {
            display: flex;
            align-items: center;
            height: px2rem(40);
            padding: 0 px2rem(20);
            color: #FF3F3F;
            font-size: px2rem(24);
            line-height: px2rem(40);
            margin-top: px2rem(24);
            .symbol-wrap {
                margin-right: px2rem(12);
                display: flex;
                justify-content: center;
                align-items: center;
                .ui-symbol {
                    height: px2rem(24);
                    width: px2rem(24);
                }
            }
            p {
                height: .4rem;
                line-height: .4rem;
            }
        }
        .btn-wrap{
            padding: 0 px2rem(40);
            margin-top: px2rem(96);
        }
    }
</style>



