<template>
    <div class="step-progress-wrap">
        <div class="item" v-for="(item, index) in stepList" :key="index">
            <div class="point-wrap">
                <div class="mask-left" v-show="index === 0"></div>
                <transition name="fade">
                    <div class="point-active" v-if="index <= activeIndex" key="active">
                        <p class="point-white"></p>
                    </div>
                    <p class="point-gray" v-else key="noActive"></p>
                </transition>
                <div class="mask-right" v-show="index === (stepList.length - 1)"></div>
            </div>
            <p class="txt" :class="{ active: index <= activeIndex }">{{item.txt}}</p>
        </div>
        <div class="line-bar-wrap">
            <div class="line-bar"
                 v-for="(item, barIndex) in (new Array(stepList.length -1))"
                 :class="{ active: barIndex < activeIndex }">
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'stepProgress',
        props: {
            // 激活的下标
            activeIndex: {
                type: Number,
                default: 0
            },
            // 步骤数组
            stepList: {
                type: Array,
                default () {
                    return [
                        {
                            txt: '信息验证'
                        },
                        {
                            txt: '密码设置'
                        },
                        {
                            txt: '注册成功'
                        }
                    ]
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .step-progress-wrap {
        height: px2rem(170);
        padding: px2rem(38) px2rem(40) px2rem(54) px2rem(40);
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        box-sizing: border-box;
        .item {
            display: flex;
            flex-direction: column;
            align-items: center;
            position: relative;
            z-index: 2;
            .point-wrap {
                width: 100%;
                height: px2rem(32);
                display: flex;
                justify-content: center;
                align-items: center;
                margin-bottom: px2rem(12);
                .mask-left, .mask-right {
                    width: 50%;
                    height: px2rem(32);
                    position: absolute;
                    z-index: 1;
                    top: 0;
                    background-color: #F0F0F0;
                }
                .mask-left{
                    left: 0;
                }
                .mask-right{
                    right: 0;
                }
                .point-gray, .point-active{
                    position: relative;
                    z-index: 2;
                }
                .point-gray {
                    width: px2rem(20);
                    height: px2rem(20);
                    background-color: #DBDBDB;
                    border-radius: 50%;
                }
                .point-active {
                    width: px2rem(32);
                    height: px2rem(32);
                    border-radius: 50%;
                    position: relative;
                    background-color: #F28300;
                    .point-white {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate3d(-50%, -50%, 0);
                        width: px2rem(8);
                        height: px2rem(8);
                        background-color: #ffffff;
                        border-radius: 50%;
                    }
                }
            }
            .txt {
                font-size: px2rem(24);
                line-height: px2rem(34);
                height: px2rem(34);
                color: #9B9B9B;
                &.active {
                    color: #F28300;;
                }
            }
        }
        .line-bar-wrap{
            position: absolute;
            top: px2rem(52);
            left: 50%;
            width: 85%;
            height:px2rem(3);
            transform: translate3d(-50%, 0, 0);
            z-index: 1;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .line-bar{
                flex: 1;
                height:px2rem(3);
                background-color: #DBDBDB;
                transition: all .3s;
                &.active{
                    background-color: #F28300;
                }
            }
        }
    }
</style>



